<template>
  <div>
    <van-nav-bar
      title="我的订单"
      @click-left="goBack"
      :fixed="true"
      left-text="返回"
      left-arrow
    >
      <template #right>
        <van-icon name="search" size="18" />
      </template>
    </van-nav-bar>
    <div class="order">
      <div class="tab">
        <van-tabs v-model="active" color="rgb(64, 174, 54)" swipeable :sticky="true">
          <van-tab v-for="(item, index) in orderState" :key="index" :title="item.title">
          </van-tab>
        </van-tabs>
      </div>
      <div class="order-list">
        <div class="order-item" v-for="(item, index) in orderList" :key="index">
          <van-card
            style="width: 100%"
            v-for="(orderGoods, index) in item.orderGoodsList"
            :key="index"
            :num="orderGoods.num"
            :price="orderGoods.goods.goodsPrice"
            :desc="'订单号: ' + item.orderNumber"
            :title="orderGoods.goods.goodsName"
            :thumb="orderGoods.goods.goodsImage"
            @click="goOrderDetail(item.orderIdStr)"
          >
            <template #footer v-if="comment">
              <van-button
                size="small"
                hairline
                type="primary"
                @click="goComment(goods, item.orderIdStr)"
                >去评价</van-button
              >
            </template>
          </van-card>
          <div class="item-btn" v-if="!comment">
            <div
              class="btn-logistics"
              v-if="item.orderStatus != '待支付' && item.orderStatus != '待评价'"
            >
              <van-button
                size="small"
                @click="receiveGoods(item.orderIdStr)"
                hairline
                type="primary"
                >确认收货</van-button
              >
            </div>

            <div
              class="btn-refund"
              v-if="item.orderStatus != '待支付' && item.orderStatus != '已完成'"
            >
              <van-button plain size="small" color="black" hairline type="primary"
                >查看物流</van-button
              >
            </div>
            <div class="btn-refund" v-if="item.orderStatus == '待支付'">
              <van-button plain size="small" color="black" hairline type="primary"
                >修改地址</van-button
              >
            </div>
            <div class="btn-appraise" v-if="item.orderStatus != '已完成'">
              <van-button plain size="small" color="black" hairline type="primary"
                >申请退款</van-button
              >
            </div>
            <div class="btn-logistics" v-if="item.orderStatus == '待支付'">
              <van-button @click="defray(item)" size="small" hairline type="primary"
                >继续支付</van-button
              >
            </div>
          </div>
        </div>
      </div>
    </div>
    <van-action-sheet v-model="show" title="确认支付">
      <div class="content">
        <div class="money">
          <span class="yuan">￥</span>
          <span class="price">{{ goodsPrice }}</span>
        </div>
        <div class="order-info">
          <van-cell-group>
            <van-field label="订单编号" :value="order.orderNumber" readonly />
            <van-field label="付款方式" :value="order.paymentMethod" readonly />
          </van-cell-group>
        </div>
        <div class="pay-btn">
          <van-button type="info" round @click="payment" size="large"
            >确认付款</van-button
          >
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import {
  Tab,
  Tabs,
  NavBar,
  Icon,
  Card,
  Button,
  ActionSheet,
  Toast,
  CellGroup,
  Field,
  Dialog,
} from "vant";
import { getUserOrderList, payment, confirmReceiptOfGoods } from "../../api/api.js";
export default {
  name: "Order",
  components: {
    "van-tabs": Tabs,
    "van-tab": Tab,
    "van-nav-bar": NavBar,
    "van-icon": Icon,
    "van-card": Card,
    "van-button": Button,
    "van-action-sheet": ActionSheet,
    "van-cell-group": CellGroup,
    "van-field": Field,
  },
  data() {
    return {
      active: 0,
      orderList: [],
      orderState: [
        {
          title: "全部",
        },
        {
          title: "待支付",
        },
        {
          title: "待发货",
        },
        {
          title: "待收货",
        },
        {
          title: "待评价",
        },
        {
          title: "已完成",
        },
      ],
      payed: true,
      show: false,
      goodsPrice: "999",
      orderIdStr: "",
      comment: false,
      order: {},
    };
  },
  methods: {
    goBack() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    goOrderDetail(orderIdStr) {
      this.$router.push({
        name: "orderDetail",
        params: {
          orderIdStr,
        },
      });
    },
    goComment(goods, orderIdStr) {
      this.$router.push({
        name: "comment",
        params: {
          active: this.active,
          goods,
          orderIdStr,
        },
      });
    },
    defray(item) {
      this.orderIdStr = item.orderIdStr;
      this.goodsPrice = 0;
      this.order.orderNumber = item.orderNumber;
      this.order.paymentMethod = item.paymentMethod;
      this.goodsPrice = item.orderAmount;
      this.show = true;
    },
    payment() {
      Toast.loading({
        message: "支付中...",
        forbidClick: true,
        duration: 2000,
        onClose: () => {
          payment(localStorage.getItem("user"), this.orderIdStr).then((val) => {
            if (val.data == "") {
              Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
                this.$router.push({
                  name: "login",
                });
              });
            }
            if (
              (val.data.paymentSuccessful != undefined) &
              (val.data.paymentSuccessful == true)
            ) {
              this.show = false;
              Toast("支付成功");

              getUserOrderList(localStorage.getItem("user"), "待支付").then((valData) => {
                this.orderList = valData.data;
              });
            }
          });
        },
      });
    },
    receiveGoods(orderIdStr) {
      confirmReceiptOfGoods(localStorage.getItem("user"), orderIdStr).then((val) => {
        if (val.data == "") {
          Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
            this.$router.push({
              name: "login",
            });
          });
        }
        if (val.data == 1) {
          this.active = 4;
        }
      });
    },
  },
  watch: {
    active() {
      let state = "";
      switch (this.active) {
        case 1:
          state = "待支付";
          this.payed = true;
          this.comment = false;
          break;
        case 2:
          state = "待发货";
          this.payed = false;
          this.comment = false;
          break;
        case 3:
          state = "待收货";
          this.payed = false;
          this.comment = false;
          break;
        case 4:
          state = "待评价";
          this.payed = false;
          this.comment = true;
          break;
        case 5:
          state = "已完成";
          this.payed = false;
          this.comment = false;
          break;
        default:
          break;
      }
      getUserOrderList(localStorage.getItem("user"), state).then((val) => {
        this.orderList = val.data;
      });
    },
  },
  mounted() {
    switch (this.$route.params.orderState) {
      case "待支付":
        this.active = 1;
        break;
      case "待发货":
        this.active = 2;
        break;
      case "待收货":
        this.active = 3;
        break;
      case "待评价":
        this.active = 4;
        break;
      case "已完成":
        this.active = 5;
        break;
      default:
        this.active = 0;
        break;
    }
    getUserOrderList(localStorage.getItem("user"), this.$route.params.orderState).then(
      (val) => {
        // if (val.data == "") {
        //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
        //     this.$router.push({
        //       name: "login",
        //     });
        //   });
        // }
        this.orderList = val.data;
      }
    );
  },
};
</script>

<style scoped>
.order {
  display: flex;
  width: 100%;
  position: relative;
}
.tab {
  position: absolute;
  width: 100%;
  top: 50px;
}
.order-list {
  position: relative;
  top: 110px;
  display: flex;
  width: 100%;
  flex-flow: wrap;
  padding: 5px;
}
.order-item {
  position: relative;
  width: 100%;
  display: flex;
  border: 1px rgba(125, 123, 123, 0.221) solid;
  flex-flow: wrap;
  margin-top: 10px;
  border-radius: 5px;
}
.item-btn {
  width: 100%;
  height: 50px;
}
.btn-logistics,
.btn-refund,
.btn-appraise {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.btn-refund {
  right: 100px;
}
.btn-appraise {
  right: 191px;
}
.content {
  padding: 16px 16px 160px;
  height: 130px;
}
.money {
  position: relative;
  height: 100px;
  display: flex;
  width: 100%;
}
.yuan,
.price {
  position: relative;
  height: 100px;
  text-align: center;
  top: 18px;
  left: 35%;
}
.price {
  font-size: 40px;
  top: 0px;
  left: 35%;
}
.order-info {
  position: relative;
  top: -30px;
}
.pay-btn {
  position: absolute;
  display: flex;
  width: 95%;
  padding: 10px;
  right: 5px;
  left: 0px;
  bottom: 10px;
}
</style>
