<template>
  <div class="selectAllComment">
    <van-nav-bar
      title="商品评价"
      :fixed="true"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="comment-list">
      <CommentUser
        v-for="(item, index) in commentList"
        :key="index"
        :user="item.user"
        :comment="item"
      />
    </div>
  </div>
</template>

<script>
import { NavBar } from "vant";
import CommentUser from "../../components/CommentUser";
import { getGoodsCommentList } from "../../api/api.js";
export default {
  name: "SelectAllComment",
  components: { "van-nav-bar": NavBar, CommentUser },
  data() {
    return {
      goods: {},
      commentList: [],
    };
  },

  methods: {
    onClickLeft() {
      this.$router.push({
        name: "goodsDetails",
        params: {
          goods: this.goods,
        },
      });
    },
  },

  mounted() {
    this.goods = this.$route.params.goods;
    getGoodsCommentList(localStorage.getItem("user"), this.goods.goodsIdStr).then(
      (val) => {
        this.commentList = val.data;
      }
    );
  },
};
</script>

<style scoped>
.comment-list {
  display: flex;
  position: relative;
  width: 90%;
  top: 50px;
  justify-content: center;
  flex-flow: wrap;
}
</style>
