<template>
  <div>
    <div class="head">
      <van-nav-bar style="top: 50px" left-text="" left-arrow @click-left="onClickLeft" />
    </div>
    <div class="head-text">
      <h2>注册</h2>
    </div>
    <div class="login-n">
      <van-field v-model="userName" placeholder="请输入用户名" />
    </div>
    <div class="login-e">
      <van-field v-model="email" placeholder="请输入邮箱" />
    </div>
    <div class="login-p">
      <van-field v-model="code" placeholder="请输入验证码" />
    </div>
    <button
      class="code"
      v-bind:class="{ 'code-color': isInput }"
      @click="isBtn && getCode()"
    >
      {{ codeText }}
    </button>
    <div class="login">
      <button @click="register">注册</button>
    </div>
    <div class="new-user">
      <p @click="goLogin">已注册，去登录&gt;</p>
    </div>
  </div>
</template>

<script>
import { NavBar, Field, Dialog } from "vant";
import axios from "axios";
export default {
  name: "Login",
  components: { "van-nav-bar": NavBar, "van-field": Field },
  data() {
    return {
      userName: "",
      email: "",
      code: "",
      codeText: "获取验证码",
      codeTime: 60,
      isBtn: false,
      isInput: false,
    };
  },
  methods: {
    onClickLeft() {
      history.back();
    },
    register() {
      axios({
        url: "http://182.92.160.192:620/user/registration",
        method: "post",
        params: {
          userName: this.userName,
          email: this.email,
          verificationCode: this.code,
        },
      }).then((val) => {
        Dialog({ message: val.data });
      });
    },
    getCode() {
      axios({
        url: "http://182.92.160.192:620/user/getCode",
        method: "post",
        params: {
          email: this.email,
        },
      });
      let timeOut = setInterval(() => {
        if (this.codeTime >= 0) {
          this.isInput = false;
          this.codeText = this.codeTime-- + "s后重新获取";
        } else {
          clearInterval(timeOut);
          this.isBtn = !this.isBtn;
          this.codeText = "获取验证码";
          this.codeTime = 60;
          this.isInput = true;
        }
      }, 1000);
    },
    goLogin() {
      this.$router.push({
        name: "login",
      });
    },
  },
  watch: {
    email() {
      if (
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.email
        )
      ) {
        this.isInput = true;
        this.isBtn = true;
      } else {
        this.isInput = false;
        this.isBtn = false;
      }
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.main {
  display: flex;
}
.head-text {
  display: flex;
}
.head-text > h2 {
  /* 手机号码登录 */
  position: absolute;
  left: 28px;
  top: 148px;
  width: 144px;
  height: 34px;
  font-family: PingFangSC-Semibold;
  font-size: 24px;
  font-weight: 0;
  line-height: 34px;
  letter-spacing: 0px;
  color: #262424;
}
.login-e,
.login-p,
.login-n {
  display: flex;
  width: 100%;
  position: absolute;
  left: 20px;
  top: 231px;
  font-family: PingFangSC-Regular;
  font-size: 15px;
  font-weight: 0;
  line-height: 21px;
  letter-spacing: 0px;
  color: #ccc8c8;
}
.login-e {
  top: 280px;
}
.login-p {
  top: 330px;
}
.code {
  /* 获取验证码 */
  position: absolute;
  left: 285px;
  top: 345px;
  width: 80px;
  height: 17px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  font-weight: 0;
  line-height: 17px;
  border: none;
  background-color: #fff;
  text-align: right;
  letter-spacing: 0px;
  color: #ccc8c8;
}
.login {
  display: flex;
}
.login > button {
  /* 编组 */
  position: absolute;
  left: 30px;
  top: 400px;
  width: 315px;
  height: 45px;
  border-radius: 22.5px;
  background-color: rgba(64, 174, 54, 1);
  border: 0.1px solid rgba(150, 150, 150, 1);
  color: #fff;
}

.new-user > p {
  /* 编组 2 */
  position: absolute;
  left: 144px;
  top: 470px;
  width: 100px;
  height: 20px;
  border: NaNpx solid #262424;
  color: rgba(102, 98, 98, 1);
  font-size: 13px;
  text-align: center;
}
.code-color {
  color: rgba(233, 185, 33, 1);
}
</style>
