<template>
  <div class="nvigation">
    <van-tabbar
      style="bottom: -1px"
      :safe-area-inset-bottom="true"
      :placeholder="true"
      v-model="active"
      active-color="#40AE36"
    >
      <van-tabbar-item v-for="(item, index) in icon" :key="index" replace :to="item.to">
        <span>{{ item.gotoName }}</span>
        <template #icon>
          <img :src="item.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>

    <div class="route-view">
      <keep-alive>
        <router-view />
      </keep-alive>
    </div>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  name: "Navigation",
  components: { "van-tabbar": Tabbar, "van-tabbar-item": TabbarItem },
  data() {
    return {
      active: 0,
      icon: [
        {
          gotoName: "首页",
          inactive: require("../assets/首页.png"),
          to: "/home",
        },
        {
          gotoName: "购物车",
          inactive: require("../assets/购物车.png"),
          to: "/shoppingCart",
        },
        {
          gotoName: "我的",
          inactive: require("../assets/我的.png"),
          to: "/personalCenter",
        },
      ],
    };
  },
  mounted() {
    this.active = this.$route.params.active;
  },
};
</script>

<style scoped>
.nvigation {
  position: relative;
  width: 100%;
  height: 50px;
  bottom: 0;
}
</style>
