<template>
  <div class="cart">
    <van-nav-bar :title="title()" :fixed="true" />
    <div class="manage">
      <p v-text="manage ? '取消' : '管理'" @click="manage = !manage"></p>
    </div>
    <van-checkbox-group v-model="result" ref="checkboxGroup">
      <div class="shopping-cart">
        <div class="cart-item">
          <van-card
            v-for="(item, index) in shoppingCartList"
            :key="index"
            :num="item.goodsNum"
            :title="item.goods.goodsName"
            :price="item.goods.goodsPrice"
            desc="描述信息"
            :thumb="item.goods.goodsImage"
            @click-thumb="goDetail(item.goods)"
          >
            <template #tags>
              <van-checkbox
                class="slection"
                :name="item"
                @click="addpriceTotal(item.goods.goodsPrice, item)"
              ></van-checkbox>
            </template>
          </van-card>
        </div>
      </div>
    </van-checkbox-group>
    <van-submit-bar
      class="submit-order"
      :price="priceTotal"
      :button-text="manage ? '删除' : '去付款'"
      @submit="onSubmit"
    >
      <van-checkbox v-model="checkedAll" @click="checked">全选</van-checkbox>
    </van-submit-bar>
  </div>
</template>

<script>
import { getUserShoppingCart, removeUserShoppingCartGoods } from "../../api/api.js";
import {
  NavBar,
  SwipeCell,
  Card,
  Button,
  SubmitBar,
  Checkbox,
  CheckboxGroup,
  Dialog,
} from "vant";
export default {
  name: "ShoppingCart",
  components: {
    "van-nav-bar": NavBar,
    "van-card": Card,
    "van-swipe-cell": SwipeCell,
    "van-button": Button,
    "van-submit-bar": SubmitBar,
    "van-checkbox": Checkbox,
    "van-checkbox-group": CheckboxGroup,
  },
  data() {
    return {
      shoppingCartList: [],
      disabled: false,
      checkedAll: false,
      manage: false,
      priceTotal: 0,
      result: [],
    };
  },
  methods: {
    title() {
      return "购物车(" + this.shoppingCartList.length + ")";
    },
    goDetail(goods) {
      if (!this.manage) {
        this.$router.push({
          name: "goodsDetails",
          params: {
            goods,
            isHome: 1,
          },
        });
      }
    },
    onSubmit() {
      if (this.result.length > 0) {
        if (this.manage) {
          Dialog.confirm({
            // title: "标题",
            message: "您确认删除吗？",
          }).then(() => {
            //删除购物车商品
            const removeList = new Array();
            for (let i = 0; i < this.result.length; i++) {
              removeList.push(this.result[i].shoppingCartIdStr);
            }
            removeUserShoppingCartGoods(localStorage.getItem("user"), removeList).then(
              (val) => {
                this.shoppingCartList = val.data.shoppingCartList;
                this.priceTotal = 0;
              }
            );
          });
        } else {
          this.$router.push({
            name: "submitOrder",
            params: {
              shoppingCartList: this.result,
              price: this.priceTotal,
            },
          });
        }
      } else {
        Dialog.alert({
          // title: "标题",
          message: "请先选择商品！",
        }).then(() => {
          // on close
        });
      }
    },
    checked() {
      if (this.checkedAll) {
        this.$refs.checkboxGroup.toggleAll(true);
        let price = 0;
        for (let i = 0; i < this.shoppingCartList.length; i++) {
          price += this.shoppingCartList[i].goods.goodsPrice;
        }
        this.priceTotal = price * 100;
      } else {
        this.$refs.checkboxGroup.toggleAll();
        this.priceTotal = 0;
      }
    },
    addpriceTotal(price, shoppingCart) {
      let res = this.result.includes(shoppingCart);
      if (res) {
        this.priceTotal = this.priceTotal + price * 100;
      } else {
        this.priceTotal = this.priceTotal - price * 100;
      }
    },
  },
  watch: {
    result() {
      if (this.result.length == this.shoppingCartList.length) {
        this.checkedAll = true;
      } else {
        this.checkedAll = false;
      }
    },
  },
  mounted() {
    getUserShoppingCart(localStorage.getItem("user")).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.shoppingCartList = val.data;
    });

    this.$store.state.shoppingCartNum = this.shoppingCartList.length;
  },
};
</script>

<style scoped>
.cart {
  position: relative;
  display: flex;
  margin-bottom: 160px;
  top: 10px;
}
.shopping-cart {
  position: relative;
  /* display: flex; */
  width: 100%;
  top: 50px;
  left: 10px;
}
.cart-item {
  position: relative;
  width: 350px;
}
.goods-card {
  margin: 0;
  background-color: #fff;
}
.submit-order {
  position: fixed;
  width: 100%;
  height: 50px;
  bottom: 50px;
}
.manage {
  position: fixed;
  top: 10px;
  z-index: 999;
  right: 10px;
}
</style>

<style scopde>
.slection {
  position: absolute;
  top: 0px;
  left: -100px;
}
</style>
