<template>
  <div class="addressManagement">
    <van-nav-bar
      title="我的地址"
      left-text="返回"
      left-arrow
      :right-text="management ? '取消' : '管理'"
      @click-left="onClickLeft"
      @click-right="management = !management"
      :fixed="true"
    />
    <div class="addresslist">
      <van-address-list
        v-model="chosenAddressId"
        :list="addressList"
        default-tag-text="默认"
        @add="onAdd"
        @edit="onEdit"
        :add-button-text="management ? '删除' : '新增地址'"
      />
    </div>
  </div>
</template>

<script>
import { NavBar, AddressList, Notify, Dialog } from "vant";
import { getUserShoppingAddress, removeUserShoppingAddress } from "../../api/api.js";
export default {
  name: "AddressManagement",
  components: { "van-nav-bar": NavBar, "van-address-list": AddressList },
  data() {
    return {
      management: false,
      addressList: [],
      chosenAddressId: "",
      tempChosenAddressId: "",
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    onAdd() {
      if (!this.management) {
        this.$router.push({
          name: "editAddressAndAdd",
          params: {
            isAdd: true,
          },
        });
      } else {
        removeUserShoppingAddress(
          localStorage.getItem("user"),
          this.chosenAddressId
        ).then((val) => {
          if (val.data == "") {
            Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
              this.$router.push({
                name: "login",
              });
            });
          }
          if (val.data.deleteNum >= 1) {
            Notify({ type: "success", message: "删除成功", duration: 500 });
            let tempList = val.data.shoppingAddressList;
            for (let i = 0; i < tempList.length; i++) {
              let tempObj = {
                id: tempList[i].shoppingAddressIdStr,
                name: tempList[i].consignee,
                tel: tempList[i].consigneePhone,
                address: tempList[i].shoppingAddress,
                isDefault: tempList[i].defaulted,
              };
              if (tempList[i].defaulted == 1) {
                this.chosenAddressId = tempList[i].shoppingAddressIdStr;
              }
              this.addressList.push(tempObj);
            }
          } else {
            Notify({ type: "danger", message: "删除失败", duration: 500 });
          }
        });
      }
    },
    onEdit(item) {
      if (!this.management) {
        this.$router.push({
          name: "editAddressAndAdd",
          params: {
            addressInfo: item,
            isAdd: false,
          },
        });
      }
    },
  },
  watch: {
    management() {
      if (this.management) {
        this.tempChosenAddressId = this.chosenAddressId;
        this.chosenAddressId = "";
      } else {
        this.chosenAddressId = this.tempChosenAddressId;
      }
    },
  },
  mounted() {
    getUserShoppingAddress(localStorage.getItem("user"), 0).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      let tempList = val.data;
      for (let i = 0; i < tempList.length; i++) {
        let tempObj = {
          id: tempList[i].shoppingAddressIdStr,
          name: tempList[i].consignee,
          tel: tempList[i].consigneePhone,
          address: tempList[i].shoppingAddress,
          isDefault: tempList[i].defaulted,
        };
        if (tempList[i].defaulted == 1) {
          this.chosenAddressId = tempList[i].shoppingAddressIdStr;
        }
        this.addressList.push(tempObj);
      }
    });
  },
};
</script>

<style scoped>
.addressManagement {
  display: flex;
  position: relative;
}
.addresslist {
  display: flex;
  position: relative;
  top: 50px;
}
.van-address-list__bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  box-sizing: border-box;
  width: 100%;
  padding: 0 16px;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
}
</style>
