import axios from "axios";

// const http = "http://localhost:8080";
const http = "http://182.92.160.192:620";


//获取验证码
export const getCode = (email) => {
  return axios({
    url: http + "/user/getCode",
    method: "post",
    params: {
      email: email,
    },
  });
};

//登录
export const login = (email, verificationCode) => {
  return axios({
    url: http + "/user/login",
    method: "post",
    params: {
      email: email,
      verificationCode: verificationCode,
    },
  });
};

