//修改用户头像
export const modifyUserAvatar = (file) => {
  return axios.post(`/user/modifyUserAvatar`, file, {
    headers: {
      'accept': '*/*',
      'Content-Type': 'multipart/form-data;boundary=' + new Date().getTime()
    },
  })

};


