<template>
  <div class="orderDetail">
    <div class="back">
      <van-nav-bar
        title="订单详情"
        left-text="返回"
        left-arrow
        :fixed="true"
        @click-left="onClickLeft"
      />
    </div>

    <div class="bgc">
      <div class="order-state">
        <div class="inco-box">
          <img :src="stateImg.img" alt="" />
        </div>
        <span class="state">{{ stateImg.state }}</span>
        <span class="state-text">{{ stateImg.text }}</span>
      </div>
    </div>
    <div class="address-box">
      <div class="address-img">
        <img :src="mapPositioning" alt="" />
      </div>
      <div class="consignee-info">
        <span>{{ shoppingAddress.consignee }}</span>
        <span>{{ shoppingAddress.consigneePhone }}</span>
      </div>
      <div class="shoppingAddress-info">
        <span>{{ shoppingAddress.shoppingAddress }}</span>
      </div>
    </div>
    <div class="order-goods-list">
      <div
        class="order-goods-item"
        v-for="(orderGoods, index) in order.orderGoodsList"
        :key="index"
      >
        <van-card
          :num="orderGoods.num"
          :price="orderGoods.goods.goodsPrice"
          :desc="orderGoods.goods.goodsFirstClass"
          :title="orderGoods.goods.goodsName"
          :thumb="orderGoods.goods.goodsImage"
        />
      </div>
    </div>
    <div class="order-info">
      <span class="total-amount-text">商品小计</span>
      <div class="total-box">
        <span class="yuan">￥</span>
        <span class="total-amount">{{ order.orderAmount }}</span>
      </div>
      <span class="preferential-amount-text">优惠金额</span>
      <span class="preferential-amount">0</span>
      <div class="split">
        <van-divider />
      </div>

      <span class="payment-amount-text">应付金额合计</span>
      <div class="amount-box">
        <span class="yuan2">￥</span>
        <span class="payment-amount">{{ order.orderAmount }}</span>
      </div>
    </div>
    <div class="order-number-info">
      <div class="item" v-for="(item, index) in orderNumberInfo" :key="index">
        <span class="item-text">{{ item.text }}</span>
        <div class="item-info-box">
          <span class="item-info">{{ item.info }}</span>
        </div>
      </div>
    </div>
    <div class="again-box">
      <div class="again-btn">
        <van-button plain round color="rgb(57, 57, 57)" type="primary" size="small"
          >再来一单</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { NavBar, Card, Divider, Button, Dialog } from "vant";
import { getUserOrderInfo, getShoppingAddressInfo } from "../../api/api.js";
export default {
  name: "OrderDetail",
  components: {
    "van-nav-bar": NavBar,
    "van-card": Card,
    "van-divider": Divider,
    "van-button": Button,
  },
  data() {
    return {
      order: {},
      shoppingAddress: {},
      active: 1,
      orderState: [
        {
          state: "待支付",
          img: require("../../assets/待支付.png"),
          text: "还未支付，请及时支付",
        },
        {
          state: "待发货",
          img: require("../../assets/待发货.png"),
          text: "已付款，等待商家发货",
        },
        {
          state: "待收货",
          img: require("../../assets/待收货.png"),
          text: "商品已发货，注意查收",
        },
        {
          state: "待评价",
          img: require("../../assets/待评价.png"),
          text: "商品期待您的评价",
        },
        {
          state: "已完成",
          img: require("../../assets/已完成.png"),
          text: "商品不错？再来一单",
        },
      ],
      mapPositioning: require("../../assets/定位.png"),
    };
  },
  computed: {
    stateImg() {
      switch (this.order.orderStatus) {
        case "待支付":
          return this.orderState[0];
        case "待发货":
          return this.orderState[1];
        case "待收货":
          return this.orderState[2];
        case "待评价":
          return this.orderState[3];
        case "已完成":
          return this.orderState[4];
      }
    },
    allGoodsNum() {
      let num = 0;
      for (let i = 0; i < this.order.orderGoodsList.length; i++) {
        num += this.order.orderGoodsList[i].num;
      }
      return num;
    },
    orderNumberInfo() {
      return [
        {
          text: "下单时间",
          info: this.order.gmtCreate,
        },
        {
          text: "订单编号",
          info: this.order.orderNumber,
        },
        {
          text: "支付方式",
          info: this.order.paymentMethod,
        },
        {
          text: "交易流水号",
          info: this.order.serialNumber,
        },
      ];
    },
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "order",
        params: {
          orderState: this.orderState,
        },
      });
    },
  },
  mounted() {
    let token = localStorage.getItem("user");
    getUserOrderInfo(token, this.$route.params.orderIdStr).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.order = val.data;
      console.log(this.order);
      getShoppingAddressInfo(token, this.order.shoppingAddressIdStr).then((res) => {
        this.shoppingAddress = res.data;
      });
    });
  },
};
</script>

<style scoped>
