  left: 0px;
  margin-top: 50px;
  width: 100%;
  height: 200px;
  border-radius: 0 0 640px 640px/0 0 80px 80px;
  background-image: linear-gradient(to bottom left, rgb(74, 175, 60), rgb(134, 222, 104));
}
.order-state {
  /* 提示 */
  position: absolute;
  left: 125px;
  top: 70px;
  width: 176px;
  height: 70px;
}
.inco-box {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 10px;
  left: 20px;
}
.inco-box > img {
  width: 100%;
  height: 100%;
}
.state {
  position: absolute;
  left: 60px;
  top: 10px;
  font-size: 23px;
}
.state-text {
  position: absolute;
  bottom: 5px;
  left: 10px;
}
.address-box {
  /* 地址 */
  margin-top: -80px;
  width: 380px;
  height: 100px;
  border-radius: 20px;
  background-color: #fff;
}
.address-img {
  position: absolute;
  width: 30px;
  height: 30px;
  left: 30px;
  margin-top: 10px;
}
