  margin-left: 50px;
  margin-top: 10px;
  display: flex;
  font-weight: 600;
}
.steps {
  position: relative;
  top: 300px;
  width: 100%;
  height: 58px;
  z-index: 9999999;
}
.order-goods-list {
  display: flex;
  margin-top: 10px;
  width: 90%;
  padding: 10px;
  justify-content: center;
  flex-flow: wrap;
  border: 1px rgba(193, 190, 190, 0.262) solid;
  background-color: #fff;
}
.order-goods-item {
  width: 100%;
}
.order-info {
  position: relative;
  margin-top: 15px;
  width: 90%;
  height: 120px;
  padding: 10px;
  background-color: #fff;
  border: 1px rgba(193, 190, 190, 0.262) solid;
  border-radius: 10px;
}
.total-amount-text {
  color: grey;
}
.total-box {
  position: absolute;
