<template>
  <div>
    <van-nav-bar
      title="确认订单"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      :fixed="true"
    />
    <div class="submit-order">
      <!-- 收货地址卡片 -->
      <div class="address-box">
        <div>
          <span>{{ shoppingAddress.consignee }}</span>
          <span>{{ shoppingAddress.consigneePhone }}</span>
        </div>
        <div>
          <span>{{ shoppingAddress.shoppingAddress }}</span>
        </div>
      </div>
      <div class="order-list">
        <van-card
          v-for="(item, index) in shoppingCartList"
          :key="index"
          :num="item.goodsNum"
          :price="item.goods.goodsPrice"
          desc="描述信息"
          :title="item.goods.goodsName"
          :thumb="item.goods.goodsImage"
          style="width: 100%"
        />
      </div>
      <div class="payment">
        <van-radio-group v-model="radioWallet">
          <van-cell-group>
            <van-cell title="生鲜钱包" style="width: 400px">
              <template #icon>
                <van-icon name="gold-coin-o" class="search-icon" />
              </template>

              <template #right-icon>
                <van-radio name="1" />
              </template>
            </van-cell>
            <van-cell title="生鲜钱包" style="width: 400px">
              <template #icon>
                <van-icon name="gold-coin-o" class="search-icon" />
              </template>

              <template #right-icon>
                <van-radio name="2" />
              </template>
            </van-cell>
            <van-cell title="生鲜钱包" style="width: 400px">
              <template #icon>
                <van-icon name="gold-coin-o" class="search-icon" />
              </template>

              <template #right-icon>
                <van-radio name="3" />
              </template>
            </van-cell>
          </van-cell-group>
        </van-radio-group>
      </div>
      <div class="cell">
        <van-cell is-link title="选择地址" @click="userAddressListShow" />
      </div>
      <!-- 选择地址罩层 -->
      <van-action-sheet v-model="addressListShow" title="选择地址" @cancel="onCancel()">
        <div style="padding: 10px">
          <van-radio-group
            checked-color="#9c84ff"
            v-model="radio"
            v-for="item in addressList"
            :key="item.shoppingAddressIdStr"
          >
            <van-radio
              :name="item.shoppingAddressIdStr"
              @click="changeAddress(item.shoppingAddressIdStr)"
            >
              <div class="contacts-list">
                <span>{{ item.consignee }}</span>
                <span>{{ item.consigneePhone }}</span>
                <p>{{ item.shoppingAddress }}</p>
              </div>
            </van-radio>
          </van-radio-group>
        </div>
        <van-button round type="info" size="large" @click="addAddress"
          >添加地址</van-button
        >
      </van-action-sheet>
    </div>

    <div class="submit">
      <van-submit-bar :price="price" button-text="提交订单" @submit="onSubmit" />
    </div>

    <van-action-sheet v-model="show" title="确认付款">
      <div class="content">
        <div class="money">
          <span class="yuan">￥</span>
          <span class="price">{{ price / 100 }}</span>
        </div>

        <div class="order-info">
          <van-cell-group>
            <van-field label="订单编号" :value="order.orderNumber" readonly />
            <van-field label="付款方式" :value="order.paymentMethod" readonly />
          </van-cell-group>
        </div>
        <div class="pay-btn">
          <van-button type="info" round @click="paymentGoods" size="large"
            >确认付款</van-button
          >
        </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import {
  NavBar,
  Cell,
  CellGroup,
  ActionSheet,
  Radio,
  RadioGroup,
  Card,
  SubmitBar,
  Icon,
  Toast,
  Button,
  Field,
  Dialog,
} from "vant";
import { getUserShoppingAddress, submitOrder, payment } from "../../api/api.js";
export default {
  name: "SubmitOrder",
  components: {
    "van-nav-bar": NavBar,
    "van-cell": Cell,
    "van-cell-group": CellGroup,
    "van-action-sheet": ActionSheet,
    "van-radio": Radio,
    "van-radio-group": RadioGroup,
    "van-card": Card,
    "van-submit-bar": SubmitBar,
    "van-icon": Icon,
    "van-button": Button,
    "van-field": Field,
  },
  data() {
    return {
      shoppingCartList: [],
      shoppingAddress: {
        consignee: "您还未添加收货地址",
        consigneePhone: "",
        shoppingAddress: "",
      },
      addressList: [],
      // 收货列表展示状态
      addressListShow: false,
      // 选择id
      radio: "",
      price: 1000,
      //默认选择钱包
      radioWallet: "1",
      goods: {},
      goodsNum: 1,
      show: false,
      order: {},
      isDetail: false,
    };
  },
  methods: {
    onClickLeft() {
      if (this.isDetail) {
        this.$router.push({
          name: "goodsDetails",
          params: {
            goods: this.shoppingCartList[0].goods,
          },
        });
      } else {
        this.$router.push({
          name: "shoppingCart",
        });
      }
    },
    addAddress() {
      this.$router.push({
        name: "editAddressAndAdd",
        params: {
          shoppingCartList: this.shoppingCartList,
          price: this.price,
          goodsNum: this.goodsNum,
        },
      });
    },
    userAddressListShow() {
      this.addressListShow = true;
      for (let i = 0; i < this.addressList.length; i++) {
        if (
          this.addressList[i].shoppingAddressIdStr ==
          this.shoppingAddress.shoppingAddressIdStr
        ) {
          this.radio = this.shoppingAddress.shoppingAddressIdStr;
        }
      }
    },
    // 选择地址罩层的对象
    onCancel() {
      for (let i = 0; i < this.addressList.length; i++) {
        if (this.addressList[i].shoppingAddressIdStr == this.radio) {
          this.shoppingAddress = this.addressList[i];
        }
      }
    },
    changeAddress(addressIdStr) {
      for (let i = 0; i < this.addressList.length; i++) {
        if (this.addressList[i].shoppingAddressIdStr == addressIdStr) {
          this.radio = this.addressList[i].shoppingAddressIdStr;
          this.shoppingAddress = this.addressList[i];
        }
      }
    },
    onSubmit() {
      const shoppingCartIdStrList = new Array();
      for (let i = 0; i < this.shoppingCartList.length; i++) {
        shoppingCartIdStrList.push(this.shoppingCartList[i].shoppingCartIdStr);
      }
      if (this.isDetail) {
        submitOrder(
          localStorage.getItem("user"),
          this.price / 100,
          this.shoppingAddress.shoppingAddressIdStr,
          shoppingCartIdStrList,
          0,
          this.shoppingCartList[0].goods.goodsIdStr,
          this.goodsNum
        )
          .then((val) => {
            this.show = true;
            this.order = val.data.order;
          })
          .catch(() => {
            Toast.fail("服务器异常请重试！");
          });
      } else {
        submitOrder(
          localStorage.getItem("user"),
          this.price / 100,
          this.shoppingAddress.shoppingAddressIdStr,
          shoppingCartIdStrList,
          0
        )
          .then((val) => {
            // if (val.data == "") {
            //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
            //     this.$router.push({
            //       name: "login",
            //     });
            //   });
            // }
            this.show = true;
            this.order = val.data.order;
          })
          .catch(() => {
            Toast.fail("服务器异常请重试！");
          });
      }
    },
    paymentGoods() {
      Toast.loading({
        message: "支付中...",
        forbidClick: true,
        duration: 2000,
        onClose: () => {
          payment(localStorage.getItem("user"), this.order.orderIdStr)
            .then((val) => {
              // if (val.data == "") {
              //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
              //     this.$router.push({
              //       name: "login",
              //     });
              //   });
              // }
              Toast.success("支付成功");
              if (val.data.paymentSuccessful) {
                this.$router.push({
                  name: "order",
                  params: {
                    orderState: "待发货",
                  },
                });
              } else {
                Toast.fail("余额不足，请充值！");
              }
            })
            .catch(() => {
              Toast.fail("服务器异常，请重试");
            });
        },
      });
    },
  },
  mounted() {
    if (this.$route.params.isDetail != undefined) {
      this.isDetail = this.$route.params.isDetail;
      this.goodsNum = this.$route.params.goodsNum;
    }
    this.shoppingCartList = this.$route.params.shoppingCartList;
    this.price = this.$route.params.price;
    getUserShoppingAddress(localStorage.getItem("user"), 1).then((val) => {
      if (val.data == "") {
        Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
          this.$router.push({
            name: "login",
          });
        });
      }
      if (val.data[0] != null) {
        this.shoppingAddress = val.data[0];
      }
    });
    getUserShoppingAddress(localStorage.getItem("user"), 0).then((val) => {
      this.addressList = val.data;
    });
  },
};
</script>

<style scoped>
.submit-order {
  top: 50px;
  position: relative;
  height: 100vh;
}

/* 收货地址 */
.address-box {
  width: 90%;
  border-radius: 10px;
  margin: 0 auto;
  padding-bottom: 65px;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: #fff;
  box-shadow: 0px 2px 10px rgb(236, 237, 255);
}
.address-box div:nth-child(1) span {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  margin: 0 20px;
  margin-top: 10px;
}

.address-box div:nth-child(2) {
  font-size: 14px;
  margin-left: 20px;
  margin-top: 10px;
  color: rgb(118, 118, 118);
}
.cell {
  position: absolute;
  width: 90%;
  top: 100px;
  left: 5%;
  margin: 0 auto;
}
/* 地址列表罩层 */
.stepper {
  position: absolute;
  bottom: 10px;
  right: 20px;
}

.contacts-list {
  margin: 10px;
}

.contacts-list p {
  font-size: 14px;
}
.contacts-list span {
  font-size: 14px;
  margin-right: 10px;
  color: rgb(118, 118, 118);
}
.contacts-list span:nth-child(1) {
  font-size: 16px;
  font-weight: 600;
  color: #000;
}
.order-list {
  display: flex;
  width: 100%;
  position: relative;
  width: 90%;

  padding: 10px;
  left: 10px;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgb(236, 237, 255);
  flex-flow: wrap;
  margin-bottom: 100px;
}

.payment {
  position: relative;
  width: 90%;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgb(236, 237, 255);
  left: 5px;
  bottom: 100px;
}
.search-icon {
  vertical-align: middle;
  line-height: 25px;
}
.custom-title {
  position: absolute;
  left: 40px;
  vertical-align: middle;
}
.submit {
  position: fixed;
  bottom: 0;
  height: 50px;
  width: 100%;
}
.content {
  padding: 16px 16px 160px;
  height: 130px;
}
.money {
  position: relative;
  width: 212px;
  height: 60px;
  display: flex;
  left: 30%;
}
.yuan {
  position: absolute;
  font-size: 17px;
  font-weight: 500;
  bottom: 17px;
  font-weight: 600;
  left: 20px;
}
.price {
  position: absolute;
  bottom: 10px;
  left: 38px;
  font-size: 40px;
}
.pay-btn {
  position: absolute;
  bottom: 20px;
  width: 90%;
  height: 50px;
}
</style>
