<template>
  <div class="wallet">
    <van-nav-bar
      title="充值"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      :fixed="true"
    />
    <div class="wallet-input">
      <span id="text-money">充值金额</span>
      <div class="from">
        <van-cell-group>
          <van-field
            v-model="money"
            label="￥"
            placeholder="请输入充值金额"
            size="large"
            :border="false"
            :autofocus="true"
            label-width="50px"
            type="number"
            @focus="show = true"
            @blur="show = false"
          />
        </van-cell-group>
      </div>
    </div>
    <van-number-keyboard
      v-model="money"
      :show="show"
      theme="custom"
      extra-key="."
      close-button-text="充值"
      @input="onInput"
      @delete="onDelete"
      @close="close"
    />
  </div>
</template>

<script>
import { NavBar, Field, CellGroup, NumberKeyboard, Notify, Dialog } from "vant";
import { recharge } from "../../api/api.js";
export default {
  name: "Wallet",
  components: {
    "van-nav-bar": NavBar,
    "van-field": Field,
    "van-cell-group": CellGroup,
    "van-number-keyboard": NumberKeyboard,
  },
  data() {
    return {
      money: "",
      show: false,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    onInput(value) {
      this.money += value;
    },
    onDelete(value) {
      this.money -= value;
    },
    close() {
      recharge(localStorage.getItem("user"), this.money).then((val) => {
        // if (val.data == "") {
        //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
        //     this.$router.push({
        //       name: "login",
        //     });
        //   });
        // }

        if (val.data == 1) {
          Notify({ type: "success", message: "充值成功", duration: 500 });
        } else {
          Notify({ type: "danger", message: "充值失败", duration: 500 });
        }
      });
    },
  },
};
</script>

<style scoped>
.wallet {
  width: 100%;
  position: relative;
}
.wallet-input {
  position: fixed;
  width: 100%;
  height: 100px;
  top: 80px;
  padding: 5px;
}
#text-money {
  position: absolute;
  left: 30px;
  top: 10px;
  font-size: 13px;
}
.from {
  position: absolute;
  top: 30px;
  left: 20px;
}
</style>
