<template>
  <div class="comment">
    <van-nav-bar title="评价" left-text="返回" left-arrow @click-left="onClickLeft" />

    <div class="goods">
      <div class="goods-img">
        <img :src="goods.goodsImage" alt="" />
      </div>
      <span class="goods-name" v-text="goods.goodsName"></span>
    </div>
    <span class="text">评分</span>
    <div class="comment-rate">
      <van-rate
        v-model="value"
        :size="25"
        color="#ffd21e"
        void-icon="star"
        void-color="#eee"
      />
    </div>
    <span class="text2">图片</span>
    <div class="img-list">
      <van-uploader v-model="fileList" multiple :max-count="4" />
    </div>
    <span class="text3">评价</span>
    <div class="comment-text">
      <van-field
        v-model="message"
        rows="2"
        autosize
        type="textarea"
        maxlength="50"
        placeholder="请输入评价"
        show-word-limit
      />
    </div>
    <div class="btn">
      <van-button
        round
        type="info"
        size="large"
        color="rgba(64, 174, 54, 1)"
        @click="submitComment"
        >提交评价</van-button
      >
    </div>
  </div>
</template>

<script>
import { NavBar, Rate, Uploader, Field, Button, Toast, Dialog } from "vant";
import { userSubmitComment } from "../../api/api.js";
export default {
  name: "Comment",
  components: {
    "van-nav-bar": NavBar,
    "van-rate": Rate,
    "van-uploader": Uploader,
    "van-field": Field,
    "van-button": Button,
  },
  data() {
    return {
      orderState: "",
      goods: {},
      value: 0,
      fileList: [],
      message: "",
      orderIdStr: "",
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "order",
        params: {
          orderState: this.orderState,
        },
      });
    },
    submitComment() {
      const fromDta = new FormData();
      for (let i = 0; i < this.fileList.length; i++) {
        fromDta.append("files", this.fileList[i].file);
      }

      fromDta.append("token", localStorage.getItem("user"));

      fromDta.append("orderIdStr", this.orderIdStr);
      fromDta.append("commentText", this.message);
      fromDta.append("score", this.value);
      fromDta.append("goodsIdStr", this.goods.goodsIdStr);
      userSubmitComment(fromDta).then((val) => {
        // if (val.data == "") {
        //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
        //     this.$router.push({
        //       name: "login",
        //     });
        //   });
        // }
        if (val.data == 1) {
          Toast("评论成功");
          this.onClickLeft();
        }
      });
    },
  },
  mounted() {
    this.goods = this.$route.params.goods;
    this.orderIdStr = this.$route.params.orderIdStr;
    switch (this.$route.params.active) {
      case 0:
        this.orderState = "";
        break;
      case 4:
        this.orderState = "待评价";
        break;
      default:
        break;
    }
  },
};
</script>

<style scoped>
.comment {
  width: 100%;
  /* height: 100vh; */
}
.goods {
  /* 商品 */
  position: absolute;
  left: 15px;
  top: 88px;
  width: 90%;
  height: 70px;
  border: 1px rgba(146, 143, 143, 0.188) solid;
  border-radius: 5px;
}
.goods-img {
  position: absolute;
  width: 30%;
  height: 100%;
}
.goods-img > img {
  position: absolute;
  width: 100%;
  height: 100%;
}
.goods-name {
  position: absolute;
  left: 130px;
  display: flex;
  top: 20px;
  font-size: 13px;
  font-weight: 500;
  color: #333333;
}
.text {
  /* 评分 */
  position: relative;
  left: 35px;
  top: 150px;
  width: 28px;
  height: 20px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
  letter-spacing: 0px;
  color: #333333;
}
.comment-rate {
  /* Star */
  position: relative;
  left: 35px;
  top: 160px;
  width: 220px;
  height: 40px;
}
.text2 {
  /* 图片 */
  position: relative;
  left: 35px;
  top: 180px;
  width: 28px;
  height: 20px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
  letter-spacing: 0px;
  color: #333333;
}
.img-list {
  position: relative;
  width: 90%;
  height: 100px;
  top: 200px;
  left: 35px;
}
.text3 {
  position: relative;
  left: 35px;
  top: 220px;
  width: 28px;
  height: 20px;
  font-family: PingFangSC-Regular;
  font-size: 14px;
  font-weight: normal;
  line-height: 20px;
  letter-spacing: 0px;
  color: #333333;
}
.comment-text {
  /* input */
  position: relative;
  left: 35px;
