<template>
  <div>
    <div class="home">
      <div class="position">
        <div class="position-img">
          <img :src="mapPositioning" alt="" />
        </div>
        <div class="address">
          <p>海淀区中关村大厦</p>
        </div>
        <div class="position-select">
          <img :src="selectAddress" alt="" />
        </div>
        <div class="scan-code">
          <img :src="scanCode" alt="" />
        </div>
        <div class="call">
          <img :src="call" alt="" />
        </div>
      </div>
      <div class="search">
        <van-search
          v-model="searchValue"
          shape="round"
          background="#fff"
          placeholder="请输入搜索关键词"
          @click="search"
        />
      </div>
      <div class="home-img">
        <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
          <van-swipe-item
            style="height: 150px"
            v-for="(banner, index) in bannerData"
            :key="index"
            ><img :src="banner" class="swipe-img" style="width: 100%; height: 100%"
          /></van-swipe-item>
        </van-swipe>
      </div>
      <div class="classification">
        <van-grid column-num="5" :border="false" icon-size="50px">
          <van-grid-item
            v-for="(item, index) in classification"
            :key="index"
            :icon="item.url"
            :text="item.className"
            :to="item.to"
          />
        </van-grid>
      </div>
      <div class="goodsShow" ref="goodsList">
        <div class="back-to-top" v-if="goBackFlag">
          <img :src="backToTop" alt="" />
        </div>
        <div class="item">
          <GoodsList
            v-for="(goods, index) in goodsData"
            :key="index"
            :goodsData="goods"
            :isHome="true"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Search, Swipe, SwipeItem, Grid, GridItem } from "vant";
import GoodsList from "../../components/GoodsList.vue";
import { getBanner, getHomeGoods } from "../../api/api.js";
export default {
  name: "Home",
  components: {
    "van-search": Search,
    "van-swipe": Swipe,
    "van-swipe-item": SwipeItem,
    GoodsList,
    "van-grid": Grid,
    "van-grid-item": GridItem,
  },
  data() {
    return {
      mapPositioning: require("../../assets/定位.png"),
      selectAddress: require("../../assets/三角形.png"),
      scanCode: require("../../assets/扫码.png"),
      call: require("../../assets/聊天.png"),
      backToTop: require("../../assets/回到顶部.png"),
      goodsData: [],
      classification: [
        {
          className: "水果蔬菜",
          url: require("../../assets/水果蔬菜.jpg"),
          to: {
            name: "search",
            params: {
              getClass: "水果蔬菜",
            },
          },
        },
        {
          className: "肉禽蛋品",
          url: require("../../assets/肉禽蛋品.webp"),
          to: {
            name: "search",
            params: {
              getClass: "肉禽蛋品",
            },
          },
        },
        {
          className: "海鲜水产",
          url: require("../../assets/海鲜水产.webp"),
          to: {
            name: "search",
            params: {
              getClass: "海鲜水产",
            },
          },
        },
        {
          className: "速食冷冻",
          url: require("../../assets/速食冷冻.webp"),
          to: {
            name: "search",
            params: {
              getClass: "速食冷冻",
            },
          },
        },

        {
          className: "油粮米面",
          url: require("../../assets/油粮米面.webp"),
          to: {
            name: "search",
            params: {
              getClass: "油粮米面",
            },
          },
        },
      ],
      searchValue: "",
      bannerData: [],
      goBackFlag: false,
    };
  },
  methods: {
    search() {
      this.$router.push({
        name: "search",
      });
    },
  },
  mounted() {
    getBanner().then((val) => {
      this.bannerData = val.data;
    });
    getHomeGoods().then((val) => {
      this.goodsData = val.data;
    });
  },
};
</script>

