/**
 * 录路由器
 */
import Vue from "vue";
import VueRouter from "vue-router";

//引入组件
import Hello from "../page/hello/Hello.vue";
import Login from "../page/hello/Login.vue";
import Register from "../page/hello/Register";
import Navigation from "../components/Navigation";
import Home from "../page/home/Home";
import PersonalCenter from "../page/personalCenter/PersonalCenter";
import Search from "../page/search/Search";
import ShoppingCart from "../page/shoppingCart/ShoppingCart";
import FootprintsAndCollections from "../page/footprintsAndCollections/FootprintsAndCollections";
import GoodsDetails from "../page/goodsDetails/GoodsDetails";
import Order from "../page/order/Order";
import SubmitOrder from "../page/submitOrder/SubmitOrder";
import Wallet from "../page/wallet/Wallet";
import AddressManagement from "../page/addressManagement/AddressManagement";
import EditAddressAndAdd from "../page/editAddressAndAdd/EditAddressAndAdd";
import UpdateEmail from '../page/updateEmail/UpdateEmail'
import SelectAllComment from '../page/selectAllComment/SelectAllComment'
import Setting from '../page/setting/Setting'
import Comment from '../page/comment/Comment'
import OrderDetail from '../page/orderDetail/OrderDetail'

//使用插件
Vue.use(VueRouter);

export default new VueRouter({
  // mode: 'history',
  routes: [
    //欢迎页
    {
      name: "hello",
      path: "/",
      component: Hello,
    },
    //登录页
    {
      name: "login",
      path: "/login",
      component: Login,
    },
    //注册页
    {
      name: "register",
      path: "/register",
      component: Register,
    },
    //搜索页
    {
      name: "search",
      path: "/search",
      component: Search,
      meta: {
        keepAlive: true // 需要缓存页面
      }
    },
    //导航栏路由
    {
      name: "navigation",
      component: Navigation,
      path: "/navigation",
      redirect: "/home",
      children: [{
          name: "home",
          path: "/home",
          component: Home,
        },
        {
          name: "personalCenter",
          path: "/personalCenter",
          component: PersonalCenter,
        },
        {
          name: "shoppingCart",
          path: "/shoppingCart",
          component: ShoppingCart,
        },
      ],
    },
    //足迹和收藏
    {
      name: "footprintsAndCollections",
      path: "/personalCenter/footprintsAndCollections",
      component: FootprintsAndCollections,
    },
    //商品详情
    {
      name: "goodsDetails",
      path: "/home/goodsDetails",
      component: GoodsDetails,
    },
    //订单
    {
      name: "order",
      path: "/personalCenter/order",
      component: Order,
    },
    //提交订单页面
    {
      name: "submitOrder",
      path: "/shoppingCart/submitOrder",
      component: SubmitOrder,
    },
    //充值
    {
      name: "wallet",
      path: "/personalCenter/wallet",
      component: Wallet,
    },
    //地址管理
    {
      name: "addressManagement",
      path: "/personalCenter/addressManagement",
      component: AddressManagement,
    },
    //修改地址和新增
    {
      name: "editAddressAndAdd",
      path: "/personalCenter/addressManagement/editAddressAndAdd",
      component: EditAddressAndAdd,
    },
    //修改邮箱
    {
      name: "updateEmail",
      path: "/personalCenter/updateEmail",
      component: UpdateEmail,
    },
    //查看商品的全部评论
    {
      name: "selectAllComment",
      path: "/goodsDetail/selectAllComment",
      component: SelectAllComment,
    },
    //用户设置界面
    {
      name: "setting",
      path: "/personalCenter/Setting",
      component: Setting,
    },
    //用户评论
    {
      name: "comment",
      path: "/personalCenter/order/commentGoods",
      component: Comment,
    },
    //订单详情
    {
      name: "orderDetail",
      path: "/personalCenter/order/orderDetail",
      component: OrderDetail,
    },
  ],
});