<template>
  <div class="personalCenter">
    <div class="my-top">
      <div class="setup">
        <img @click="userSettin" :src="setting" alt="" />
      </div>
      <div class="head-image">
        <img :src="user.headImage" alt="" />
      </div>
      <div class="user-name">
        <p>{{ user.userName }}</p>
      </div>
    </div>
    <div class="user-info">
      <table>
        <tr>
          <td>余额</td>
          <td>累计消费</td>
          <td>积分</td>
          <td>优惠券</td>
        </tr>
        <tr>
          <td>{{ user.balance }}</td>
          <td>0.00</td>
          <td>0</td>
          <td>0</td>
        </tr>
      </table>
    </div>
    <div class="user-order">
      <span id="selectAll" @click="selectAllOrder">查看全部&gt;</span>
      <van-grid column-num="5" :border="false">
        <van-grid-item
          v-for="(item, index) in orderState"
          :key="index"
          :icon="item.img"
          :text="item.text"
          :to="item.to"
          :badge="item.num"
        />
        <van-grid-item :icon="okState.img" :text="okState.text" :to="okState.to" />
      </van-grid>
    </div>
    <div class="user-set">
      <van-cell-group>
        <van-cell
          style="line-height: 40px"
          v-for="(item, index) in userFunction"
          :key="index"
          :title="item.title"
          size="large"
          center
          is-link
          :to="item.to"
          :icon="item.icon"
        />
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import { getUserInfo, getUserAllOrderStateNumber } from "../../api/api.js";
import { Grid, GridItem, Cell, CellGroup } from "vant";
export default {
  name: "PersonalCenter",
  components: {
    "van-grid": Grid,
    "van-grid-item": GridItem,
    "van-cell": Cell,
    "van-cell-group": CellGroup,
  },
  data() {
    return {
      setting: require("../../assets/设置.png"),
      user: {
        headImage: require("../../assets/默认头像.png"),
        balance: 0.0,
        userName: "用户9527",
      },
      okState: {
        text: "已完成",
        img: require("../../assets/已完成.png"),
        to: {
          name: "order",
          params: {
            orderState: "已完成",
          },
        },
        num: 99,
      },
      orderState: [
        {
          text: "待支付",
          img: require("../../assets/待支付.png"),
          to: {
            name: "order",
            params: {
              orderState: "待支付",
            },
          },
          num: 99,
        },
        {
          text: "待发货",
          img: require("../../assets/待发货.png"),
          to: {
            name: "order",
            params: {
              orderState: "待发货",
            },
          },
          num: 99,
        },
        {
          text: "待收货",
          img: require("../../assets/待收货.png"),
          to: {
            name: "order",
            params: {
              orderState: "待收货",
            },
          },
          num: 99,
        },
        {
          text: "待评价",
          img: require("../../assets/待评价.png"),
          to: {
            name: "order",
            params: {
              orderState: "待评价",
            },
          },
          num: 99,
        },
      ],
      userFunction: [
        {
          title: "地址管理",
          to: {
            name: "addressManagement",
          },
          icon: require("../../assets/地址管理.png"),
        },
        {
          title: "充值",
          to: {
            name: "wallet",
          },
          icon: require("../../assets/我的钱包.png"),
        },
        {
          title: "我的优惠",
          to: "",
          icon: require("../../assets/我的优惠.png"),
        },
        {
          title: "我的收藏",
          to: {
            name: "footprintsAndCollections",
            params: {
              titleFlag: 0,
            },
          },
          icon: require("../../assets/我的收藏.png"),
        },
        {
          title: "我的足迹",
          to: {
            name: "footprintsAndCollections",
            params: {
              titleFlag: 1,
            },
          },
          icon: require("../../assets/足迹.png"),
        },
        {
          title: "修改邮箱",
          to: {
            name: "updateEmail",
          },
          icon: require("../../assets/修改密码.png"),
        },
      ],
    };
  },
  methods: {
    selectAllOrder() {
      this.$router.push({
        name: "order",
        params: {
          orderState: "",
        },
      });
    },
    userSettin() {
      this.$router.push({
        name: "setting",
      });
    },
  },
  mounted() {
    getUserInfo(localStorage.getItem("user")).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.user.headImage = val.data.userAvatar;
      this.user.userName = val.data.userName;
      this.user.balance = val.data.userBalance;
      this.user.balance.toFixed(2);
    });
    getUserAllOrderStateNumber(localStorage.getItem("user")).then((val) => {
      this.orderState[0].num = val.data.toBePaid;
      this.orderState[1].num = val.data.toBeDelivered;
      this.orderState[2].num = val.data.toBeReceived;
      this.orderState[3].num = val.data.toBeEvaluated;
    });
    this.active = 2;
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
.personalCenter {
  display: flex;
  position: relative;
  width: 100%;
}
.my-top {
  position: relative;
  width: 100%;
  height: 200px;
  background-color: rgb(138, 248, 113);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.setup {
  display: flex;
  position: absolute;
  width: 30px;
  height: 30px;
  right: 10px;
  top: 10px;
  z-index: 99;
}
.setup > img {
  width: 100%;
  height: 100%;
}
.head-image {
  display: flex;
  position: absolute;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  left: 20px;
  top: 50px;
  background-color: #fff;
  border: 1px rgba(165, 163, 163, 0.412) solid;
}
.head-image > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.user-name {
  position: absolute;
  display: flex;
  height: 20px;
  top: 80px;
  left: 120px;
}
.user-name > p {
  color: rgb(8, 8, 8);
  font-weight: 600;
}
.user-info {
  position: absolute;
  width: 90%;
  display: flex;
  height: 70px;
  background-color: rgba(30, 74, 10, 0.829);
  top: 150px;
  left: 20px;
  border-radius: 10px;
}
.user-info table {
  color: #fff;
  width: 100%;
  text-align: center;
}
table tr:nth-child(2) td {
  color: rgb(138, 248, 113);
}
.user-order {
  position: absolute;
  width: 100%;
  height: 70px;
  top: 250px;
}
.user-set {
  position: absolute;
  width: 100%;
  height: 300px;
  top: 350px;
}
#selectAll {
  position: absolute;
  right: 15px;
  top: -5px;
  z-index: 10;
  font-size: 10px;
  color: gray;
}
</style>
