<template>
  <div class="footprintsAndCollections">
    <div class="head">
      <van-nav-bar
        :title="titleFlag ? '我的足迹' : '我的收藏'"
        left-text="返回"
        left-arrow
        @click-left="onClickLeft"
        :fixed="true"
      />
    </div>
    <van-empty v-if="!show" :description="titleFlag ? '您的足迹为空' : '您的收藏为空'" />
    <div class="manage" v-if="show" @click="selectionGoods">
      <p v-text="manage ? '取消' : '管理'"></p>
    </div>
    <div class="goods-list" v-if="show">
      <div class="goods-item" v-for="(goods, index) in goodsList" :key="index">
        <GoodsList :goodsData="goods.goods" :isHome="false"></GoodsList>

        <div class="rdio" v-if="manage">
          <van-checkbox-group v-model="removeList" ref="checkboxGroup">
            <van-checkbox
              :name="isCollect ? goods.collectIdStr : goods.footprintIdStr"
              v-model="checked"
            ></van-checkbox>
          </van-checkbox-group>
        </div>
      </div>
      <div class="remove" v-if="manage">
        <div class="Select-all">
          <!-- <van-checkbox v-model="checkedAll">全选</van-checkbox> -->
          <div class="remove-btn">
            <van-button type="danger" round size="large" @click="onRemove"
              >删除</van-button
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { NavBar, Checkbox, Button, CheckboxGroup, Dialog, Empty } from "vant";
import GoodsList from "../../components/GoodsList.vue";
import {
  getUserCollect,
  getUserFootprint,
  removeUserCollect,
  removeUserFootprint,
} from "../../api/api.js";
export default {
  name: "FootprintsAndCollections",
  components: {
    "van-nav-bar": NavBar,
    "van-checkbox": Checkbox,
    "van-button": Button,
    "van-checkbox-group": CheckboxGroup,
    "van-empty": Empty,
    GoodsList,
  },
  data() {
    return {
      titleFlag: 0,
      goodsList: [],
      manage: false,
      checked: true,
      isCollect: false,
      removeList: [],
      checkedAll: false,
      show: true,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    onRemove() {
      Dialog.confirm({
        // title: "",
        message: "您确认删除吗",
      })
        .then(() => {
          if (this.isCollect == true) {
            removeUserCollect(localStorage.getItem("user"), this.removeList).then(
              (val) => {
                // if (val.data == "") {
                //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
                //     this.$router.push({
                //       name: "login",
                //     });
                //   });
                // }
                this.manage = false;
                this.goodsList = val.data.collectList;
              }
            );
          } else {
            removeUserFootprint(localStorage.getItem("user"), this.removeList).then(
              (val) => {
                // if (val.data == "") {
                //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
                //     this.$router.push({
                //       name: "login",
                //     });
                //   });
                // }
                this.manage = false;
                this.goodsList = val.data.footprintList;
              }
            );
          }
        })
        .catch(() => {
          console.log(this.removeList);
        });
    },
    selectionGoods() {
      this.manage = !this.manage;
    },
  },
  watch: {
    manage() {
      if (this.manage == false) {
        this.removeList = [];
      }
    },
  },
  mounted() {
    this.titleFlag = this.$route.params.titleFlag;
    if (this.$route.params.titleFlag == 0) {
      this.isCollect = true;
      getUserCollect(localStorage.getItem("user")).then((val) => {
        if (val.data == "") {
          Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
            this.$router.push({
              name: "login",
            });
          });
        }
        if (val.data.length > 0) {
          this.show = true;
          this.goodsList = val.data;
        } else {
          this.show = false;
        }
      });
    } else {
      this.isCollect = false;
      getUserFootprint(localStorage.getItem("user")).then((val) => {
        if (val.data == "") {
          Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
            this.$router.push({
              name: "login",
            });
          });
        }
        if (val.data.length > 0) {
          this.show = true;
          this.goodsList = val.data;
        } else {
          this.show = false;
        }
      });
    }
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
.footprintsAndCollections {
  position: relative;
}
.head {
  position: absolute;
  width: 100%;
}
.manage {
  position: fixed;
  width: 40px;
  height: 40px;
  top: 10px;
  right: 0;
  z-index: 999;
}
.goods-list {
  display: flex;
  position: relative;
  width: 95%;
  top: 50px;
  bottom: 60px;
  justify-content: left;
  flex-flow: wrap;
  left: 20px;
}

.remove {
  position: fixed;
  width: 100%;
  height: 60px;
  bottom: 10px;
  background-color: #fff;
}
.Select-all {
  position: absolute;
  top: 20px;
  left: 20px;
}
.remove-btn {
  position: absolute;
  width: 100px;
  top: -15px;
  left: 250px;
}
.rdio {
  position: relative;
  top: -180px;
  left: 10px;
}
</style>
