<template>
  <div>
    <div class="goodsList" @click="goDetails">
      <div class="show">
        <img :src="goodsData.goodsImage" alt="" />
        <p>{{ goodsData.goodsName }}</p>
        <p id="price">￥{{ goodsData.goodsPrice }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup } from "vant";
export default {
  name: "GoodsList",
  data() {
    return {
      radio: "1",
    };
  },
  components: { "van-checkbox": Checkbox, "van-checkbox-group": CheckboxGroup },
  props: ["goodsData", "isHome"],
  methods: {
    goDetails() {
      this.$router.push({
        name: "goodsDetails",
        params: {
          goods: this.isHome ? this.goodsData : this.goodsData.goods,
          isHome: this.isHome ? 0 : 2,
        },
      });
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.goodsList {
  display: flex;
  width: 150px;
}
.show {
  height: 140px;
  padding-top: 0;
  padding-left: 0;
  padding-right: 0;
  width: 140px;
  border-radius: 10px;
  margin: 5px;
  border: solid 1px rgba(230, 228, 228, 0.678);
  background-color: #fff;
}
.show > img {
  width: 100%;
  height: 60%;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 0 1px rgba(240, 239, 239, 0.678);
}
.show > p {
  position: relative;
  text-align: left;
  top: 10px;
  font-size: 12px;
  font-weight: 600;
  color: rgb(83, 81, 81);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#price {
  margin-bottom: 10px;
  color: rgba(245, 87, 38, 1);
  top: 20px;
  font-size: 13px;
}
.bargain-price,
.goto-home-time {
  position: absolute;
  width: 30px;
  height: 15px;
  top: 140px;
  left: 50px;
  border: solid 1px #f55726;
}
.bargain-price,
.goto-home-time > p {
  font-size: 1px;
  font-weight: 0;
  text-align: center;
  color: #f55726;
}
.goto-home-time {
  position: absolute;
  width: 80px;
  left: 90px;
  border: solid 1px rgb(138, 199, 46);
}
.goto-home-time > p {
  color: rgb(138, 199, 46);
}
</style>
