<template>
  <div class="updateEmail">
    <van-nav-bar
      :fixed="true"
      title="修改密码"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <div class="update">
      <van-form @submit="onSubmit">
        <van-field
          :readonly="true"
          v-model="oldEmail"
          name="旧邮箱"
          label="旧邮箱"
          placeholder="旧邮箱"
        />
        <van-field
          v-model="newEmail"
          name="新邮箱"
          label="新邮箱"
          placeholder="新邮箱"
          :rules="[{ required: true, message: '请填写新邮箱' }]"
        />
        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit">提交</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { NavBar, Field, Form, Button, Toast, Dialog } from "vant";
import { getUserInfo, modifyMailbox } from "../../api/api.js";
export default {
  components: {
    "van-nav-bar": NavBar,
    "van-form": Form,
    "van-field": Field,
    "van-button": Button,
  },
  data() {
    return {
      oldEmail: "9527@shengxian.com",
      newEmail: "",
    };
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    onSubmit() {
      if (this.oldEmail == this.newEmail) {
        Toast.fail("新邮箱与旧邮箱相同");
        return;
      }
      modifyMailbox(localStorage.getItem("user"), this.newEmail).then((val) => {
        if (val.data.error != undefined) {
          Toast.fail(val.data.error);
        } else {
          Toast.success("修改成功");
          setTimeout(() => {
            Toast.clear();
            this.$router.push({
              name: "personalCenter",
            });
          }, 1000);
        }
      });
    },
  },
  mounted() {
    getUserInfo(localStorage.getItem("user")).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.oldEmail = val.data.userEmail;
    });
  },
};
</script>

<style scoped>
.updateEmail {
  position: relative;
  width: 100%;
}
.update {
  top: 100px;
  position: relative;
  width: 100%;
}
</style>
