<template>
  <div class="setting">
    <van-nav-bar
      title="设置"
      :fixed="true"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />

    <div class="user-setting-info">
      <!-- 修改头像 -->
      <div class="avatar">
        <span class="avatar-text">头像</span>
        <div class="avatar-info">
          <img :src="user.userAvatar" @click="updateUser(5)" />
        </div>
        <div class="dividing-line"><van-divider :hairline="false" /></div>
      </div>

      <div class="user-info-setting">
        <div class="user-info" v-for="(item, index) in userSetting" :key="index">
          <span class="info-title">{{ item.infoTitle }}</span>
          <span
            :class="item.editable ? 'info-text' : 'info-text2'"
            @click="updateUser(index)"
            >{{ item.infoText }}</span
          >
          <div class="info-dividing-line" v-if="index != userSetting.length - 1">
            <van-divider :hairline="false" />
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="showUserName">
      <div class="updateUser">
        <van-form @submit="onSubmit">
          <van-field
            v-model="user.userName"
            name="昵称"
            label="昵称"
            placeholder="昵称"
          />
          <div style="margin: 16px">
            <van-button round block type="info" @click="onSubmit" native-type="submit"
              >保存</van-button
            >
          </div>
        </van-form>
      </div>
    </van-popup>
    <van-popup v-model="showGender" position="bottom"
      ><div class="updateGender">
        <van-picker
          title="请选择性别"
          item-height="20px"
          show-toolbar
          :columns="columns"
          @confirm="onConfirm"
          @cancel="onCancel"
        /></div
    ></van-popup>
    <van-popup v-model="showUserEmail">内容</van-popup>
    <van-popup v-model="showUserAvatarUpload">
      <div class="up">
        <div class="up-avatar">
          <van-uploader
            v-model="fileList"
            :upload-text="'上传图片'"
            accept="image/png, image/jpg"
            :max-count="1"
          />
        </div>
        <div class="upUserAvatar">
          <van-button round block type="info" @click="upUserAvatar" native-type="submit"
            >保存</van-button
          >
        </div>
      </div>
    </van-popup>
    <van-action-sheet v-model="showUserAvatar" :actions="actions" @select="onSelect" />
  </div>
</template>

<script>
import {
  NavBar,
  Divider,
  Popup,
  Form,
  Field,
  Button,
  ImagePreview,
  Picker,
  ActionSheet,
  Uploader,
} from "vant";
import { getUserInfo, modifyUser, modifyUserAvatar } from "../../api/api.js";
export default {
  name: "Setting",
  components: {
    "van-nav-bar": NavBar,
    "van-divider": Divider,
    "van-popup": Popup,
    "van-field": Field,
    "van-form ": Form,
    "van-button": Button,
    "van-picker": Picker,
    "van-action-sheet": ActionSheet,
    "van-uploader": Uploader,
  },
  data() {
    return {
      user: {},
      showUserName: false,
      showGender: false,
      showUserEmail: false,
      showUserAvatar: false,
      showUserAvatarUpload: false,
      columns: ["男", "女"],
      actions: [{ name: "查看大图" }, { name: "从相册中选择" }, { name: "拍照" }],
      fileList: [],
    };
  },
  computed: {
    userSetting() {
      return [
        {
          infoTitle: "昵称",
          infoText: this.user.userName,
          editable: true,
        },
        {
          infoTitle: "性别",
          infoText: this.user.gender == 1 ? "男" : "女",
          editable: true,
        },
        {
          infoTitle: "ID",
          infoText: this.user.userIdStr,
          editable: false,
        },
        {
          infoTitle: "邮箱地址",
          infoText: this.user.userEmail,
          editable: true,
        },
        {
          infoTitle: "注册时间",
          infoText: this.user.gmtCreate,
          editable: false,
        },
      ];
    },
  },
  methods: {
    onClickLeft() {
      this.$router.push({
        name: "personalCenter",
        params: {
          active: 2,
        },
      });
    },
    upUserAvatar() {
      const formData = new FormData();
      formData.append("file", this.fileList[0].file);
      formData.append("token", localStorage.getItem("user"));
      modifyUserAvatar(formData).then((val) => {
        this.user.userAvatar = val.data;
        this.showUserAvatarUpload = false;
      });
    },
    updateUser(index) {
      switch (index) {
        case 0:
          this.showUserName = true;
          break;
        case 1:
          this.showGender = true;
          break;
        case 3:
          this.showUserEmail = true;
          break;
        case 5:
          this.showUserAvatar = true;
          break;

        default:
          break;
      }
    },
    onSubmit() {
      this.showUserName = false;
      modifyUser(
        localStorage.getItem("user"),
        this.user.userName,
        this.user.gender,
        this.user.userEmail
      ).then((val) => {
        this.user = val.data;
      });
    },
    onConfirm(value, index) {
      if (index == 0) {
        this.user.gender = 1;
      } else {
        this.user.gender = 2;
      }
      this.showGender = false;
      modifyUser(
        localStorage.getItem("user"),
        this.user.userName,
        this.user.gender,
        this.user.userEmail
      ).then((val) => {
        this.user = val.data;
      });
    },
    onCancel() {
      this.showGender = false;
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.showUserAvatar = false;
      switch (item.name) {
        case "查看大图":
          ImagePreview({
            images: [this.user.userAvatar],
            showIndex: false,
          });
          break;
        case "从相册中选择":
          this.showUserAvatarUpload = true;
          break;
        case "拍照":
          break;
        default:
          break;
      }
    },
  },
  mounted() {
    getUserInfo(localStorage.getItem("user")).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.user = val.data;
    });
  },
};
</script>

<style scoped>
.setting {
  position: relative;
  width: 100%;
  height: 100vh;
  background: #f8f9fa;
}
.user-setting-info {
  position: relative;
  top: 70px;
  left: 5%;
  width: 90%;
  height: 380px;
  border-radius: 10px;
  background-color: #fff;
}
.avatar {
  position: absolute;
  width: 100%;
  height: 60px;
}
.avatar-text {
  position: absolute;
  top: 40%;
  left: 15px;
  font-weight: 500;
}

.avatar-info {
  position: absolute;
  top: 5px;
  width: 50px;
  height: 50px;
  right: 10px;
}
.avatar-info > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px rgba(48, 49, 48, 0.275) solid;
}
.dividing-line {
  position: absolute;
  bottom: 10px;
  width: 95%;
  height: 1px;
  left: 5%;
}
.user-info-setting {
  position: relative;
  flex-wrap: wrap;
  width: 100%;
  height: 240px;
  top: 90px;
}
.user-info {
  width: 100%;
  height: 25%;
}
.info-title {
  position: relative;
  left: 15px;
  top: 0px;
  font-weight: 500;
}
.info-text {
  position: absolute;
  display: flex;
  right: 10px;
  margin-top: -20px;
  font-weight: 600;
}
.info-dividing-line {
  position: relative;
  width: 95%;
  height: 1px;
  left: 5%;
  bottom: 0px;
}
.info-text2 {
  position: absolute;
  display: flex;
  right: 10px;
  margin-top: -20px;
  color: gray;
}
.updateUser {
  width: 300px;
  height: 120px;
}
.updateGender {
  width: 100%;
  height: 200px;
}
.up {
  position: relative;
  width: 200px;
  height: 220px;
}
.up-avatar {
  position: absolute;
  top: 40px;
  width: 80%;
  height: 80%;
  left: 55px;
}
.upUserAvatar {
  position: absolute;
  bottom: 10px;
  width: 80%;
  left: 20px;
}
</style>
