<template>
  <div>
    <div class="head">
      <van-nav-bar style="top: 50px" left-text="" left-arrow @click-left="onClickLeft" />
    </div>
    <div class="head-text">
      <h2>使用邮箱登录</h2>
    </div>
    <div class="login-e">
      <van-field v-model="email" placeholder="请输入邮箱" />
    </div>
    <div class="login-p">
      <van-field v-model="code" placeholder="请输入验证码" />
    </div>
    <button :id="isInput ? 'code-color' : 'code'" @click="isBtn && getCode()">
      {{ codeText }}
    </button>
    <div class="login">
      <button @click="login">登录</button>
    </div>
    <div class="new-user">
      <p @click="newUser">新用户注册&gt;</p>
    </div>
  </div>
</template>

<script>
import { NavBar, Field, Toast } from "vant";
import { getCode, login } from "../../api/api.js";
export default {
  name: "Login",
  components: { "van-nav-bar": NavBar, "van-field": Field },
  data() {
    return {
      email: "",
      code: "",
      codeText: "获取验证码",
      codeTime: 60,
      isBtn: false,
      isInput: false,
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    login() {
      Toast.loading({
        message: "登录中...",
        forbidClick: true,
        duration: 2000,
        onClose: () => {
          login(this.email, this.code).then((val) => {
            if (val.data != null && val.data != "") {
              Toast.success("登录成功");
              localStorage.setItem("user", val.data.token);
              this.$router.push({
                name: "navigation",
              });
            } else {
              Toast.fail("验证码错误");
            }
          });
        },
      });
    },
    getCode() {
      getCode(this.email);
      let timeOut = setInterval(() => {
        if (this.codeTime >= 0) {
          this.isInput = false;
          this.codeText = this.codeTime-- + "s后重新获取";
        } else {
          clearInterval(timeOut);
          this.isBtn = !this.isBtn;
          this.codeText = "获取验证码";
          this.codeTime = 60;
          this.isInput = true;
        }
      }, 1000);
    },
    newUser() {
      this.$router.push({
        name: "register",
      });
    },
  },
  watch: {
    email() {
      if (
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.email
        )
      ) {
        this.isInput = true;
        this.isBtn = true;
      } else {
        this.isInput = false;
        this.isBtn = false;
      }
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
.main {
  display: flex;
}
.head-text {
  display: flex;
}
.head-text > h2 {
  /* 手机号码登录 */
  position: absolute;
  left: 28px;
  top: 148px;
  width: 144px;
  height: 34px;
  font-family: PingFangSC-Semibold;
  font-size: 24px;
  font-weight: 0;
  line-height: 34px;
  letter-spacing: 0px;
  color: #262424;
}
.login-e,
.login-p {
  display: flex;
  width: 100%;
  position: absolute;
  left: 20px;
  top: 231px;
  font-family: PingFangSC-Regular;
  font-size: 15px;
  font-weight: 0;
  line-height: 21px;
  letter-spacing: 0px;
  color: orange;
}
.login-p {
  top: 280px;
}
#code {
  /* 获取验证码 */
  position: absolute;
  left: 285px;
  top: 345px;
  width: 80px;
  height: 17px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  font-weight: 0;
  line-height: 17px;
  border: none;
  background-color: #fff;
  text-align: right;
  letter-spacing: 0px;
  color: #ccc8c8;
}
.login {
  display: flex;
}
.login > button {
  /* 编组 */
  position: absolute;
  left: 30px;
  top: 346px;
  width: 315px;
  height: 45px;
  border-radius: 22.5px;
  background-color: rgba(64, 174, 54, 1);
  border: 0.1px solid rgba(150, 150, 150, 1);
  color: #fff;
}
.new-user > p {
  /* 编组 2 */
  position: absolute;
  left: 144px;
  top: 410px;
  width: 90px;
  height: 20px;
  color: rgba(102, 98, 98, 1);
  font-size: 13px;
  text-align: center;
  border: none;
  
}
#code-color {
  position: absolute;
  left: 285px;
  top: 345px;
  width: 80px;
  height: 17px;
  font-family: PingFangSC-Regular;
  font-size: 12px;
  font-weight: 0;
  line-height: 17px;
  border: none;
  background-color: #fff;
  text-align: right;
  letter-spacing: 0px;
  color: rgba(233, 185, 33, 1);
}
</style>
