<template>
  <div class="editAddressAndAdd">
    <van-nav-bar
      left-text="返回"
      :title="isAdd ? '新增地址' : '修改地址'"
      left-arrow
      @click-left="onClickLeft"
      :fixed="true"
    />
    <div class="edit-address">
      <van-address-edit
        :area-list="areaList"
        show-delete
        show-set-default
        :address-info="addressInfo"
        show-search-result
        :search-result="searchResult"
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </div>
  </div>
</template>

<script>
import { AddressEdit, NavBar, Notify, Dialog } from "vant";
import { areaList } from "@vant/area-data";
import smart from "address-smart-parse";
import {
  addUserShoppingAddress,
  modifyUserShoppingAddress,
  removeUserShoppingAddress,
} from "../../api/api.js";
export default {
  name: "EditAddressAndAdd",
  components: { "van-address-edit": AddressEdit, "van-nav-bar": NavBar },
  data() {
    return {
      areaList,
      searchResult: [],
      addressInfo: {
        id: "",
        name: "",
        tel: "",
        province: "",
        city: "",
        county: "",
        addressDetail: "",
        areaCode: "",
        postalCode: "",
        isDefault: false,
      },
      isAdd: true,
      isOrder: false,
      shoppingCartList: "",
      price: "",
      goodsNum: "",
    };
  },
  methods: {
    onSave(content) {
      if (this.isAdd) {
        addUserShoppingAddress(
          localStorage.getItem("user"),
          content.tel,
          content.province + content.city + content.county + content.addressDetail,
          content.name,
          content.isDefault ? 1 : 0
        ).then((val) => {
          if (val.data == 1) {
            Notify({ type: "success", message: "添加成功", duration: 500 });
          } else {
            Notify({ type: "danger", message: "添加失败", duration: 500 });
          }
        });
      } else {
        modifyUserShoppingAddress(
          localStorage.getItem("user"),
          content.tel,
          content.province + content.city + content.county + content.addressDetail,
          content.name,
          content.isDefault ? 1 : 0,
          content.id
        ).then((val) => {
          // if (val.data == "") {
          //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
          //     this.$router.push({
          //       name: "login",
          //     });
          //   });
          // }
          if (val.data == 1) {
            Notify({ type: "success", message: "修改成功", duration: 500 });
          } else {
            Notify({ type: "danger", message: "修改失败", duration: 500 });
          }
        });
      }
    },
    onDelete() {
      removeUserShoppingAddress(localStorage.getItem("user"), this.addressInfo.id).then(
        (val) => {
          if (val.data.deleteNum >= 1) {
            // if (val.data == "") {
            //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
            //     this.$router.push({
            //       name: "login",
            //     });
            //   });
            // }
            Notify({ type: "success", message: "删除成功", duration: 500 });
            this.$router.push({
              name: "addressManagement",
            });
          } else {
            Notify({ type: "danger", message: "删除失败", duration: 500 });
          }
        }
      );
    },

    onChangeDetail() {},
    onClickLeft() {
      if (this.isOrder) {
        this.$router.push({
          name: "submitOrder",
          params: {
            shoppingCartList: this.shoppingCartList,
            price: this.price,
            goodsNum: this.goodsNum,
          },
        });
      } else {
        this.$router.go(-1);
      }
    },
  },

  mounted() {
    if (this.$route.params.addressInfo != undefined) {
      let addressAnalysis = smart(this.$route.params.addressInfo.address);
      this.addressInfo.id = this.$route.params.addressInfo.id;
      this.addressInfo.name = this.$route.params.addressInfo.name;
      this.addressInfo.tel = this.$route.params.addressInfo.tel;
      this.addressInfo.addressDetail = this.$route.params.addressInfo.address;
      this.addressInfo.isDefault =
        this.$route.params.addressInfo.isDefault == 1 ? true : false;
      this.addressInfo.county = addressAnalysis.county;
      this.addressInfo.city = addressAnalysis.city;
      this.addressInfo.areaCode = addressAnalysis.countyCode;
      this.addressInfo.province = addressAnalysis.province;
      this.addressInfo.city = addressAnalysis.city;
      this.addressInfo.county = addressAnalysis.county;
      this.isAdd = this.$route.params.isAdd;
    }

    if (this.$route.params.shoppingCartList != undefined) {
      this.isOrder = true;
      this.shoppingCartList = this.$route.params.shoppingCartList;
      this.price = this.$route.params.price;
      this.goodsNum = this.$route.params.goodsNum;
    }
  },
};
</script>

<style>
.editAddressAndAdd {
  top: 50px;
}
.edit-address {
  position: relative;
  top: 60px;
}
</style>
