<template>
  <div>
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
      <template #right>
        <van-icon name="share-o" size="20" />
      </template>
    </van-nav-bar>
    <div class="goodsDetails">
      <div class="goods-img">
        <img :src="goods.goodsImage" alt="" />
      </div>
      <div class="text">
        <div class="price">
          <p id="price-yuan">￥</p>
          <p>{{ goods.goodsPrice }}</p>
        </div>
        <div class="goods-class">
          <van-tag plain type="primary" color="rgb(7,193,96)">{{
            goods.goodsFirstClass
          }}</van-tag>
        </div>
        <div class="goods-name">
          <p>{{ goods.goodsName }}</p>
        </div>
        <div class="inventory">
          <p>库存：{{ goods.goodsInventory }}</p>
        </div>
      </div>

      <van-action-sheet v-model="show">
        <div class="selection">
          <div class="selection-img">
            <img :src="goods.goodsImage" alt="" />
          </div>
          <div class="selection-price">
            <p id="yuan">￥</p>
            <p id="price">{{ goods.goodsPrice }}</p>
          </div>

          <div class="by-number">
            <van-cell title="选择数量" icon="shop-o">
              <!-- 使用 right-icon 插槽来自定义右侧图标 -->
              <template #right-icon>
                <van-stepper
                  v-model="byNumber"
                  input-width="40px"
                  button-size="32px"
                  disable-input
                />
              </template>
            </van-cell>
          </div>
          <div class="ok">
            <van-button
              style="width: 100%; height: 100%"
              round
              hairline
              type="primary"
              @click="addShoppingCartOrSubmitOrder"
              >确认</van-button
            >
          </div>
        </div>
      </van-action-sheet>
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" color="#ee0a24" />
        <van-goods-action-icon
          @click="goShoppingCart"
          icon="cart-o"
          text="购物车"
          :badge="shoppingCartNum"
        />
        <van-goods-action-icon
          @click="userRmOrAddCollect(goods.goodsIdStr), (collected = !collected)"
          :icon="collected ? 'star' : 'star-o'"
          :text="collected ? '已收藏' : '未收藏'"
          color="#ff5000"
        />
        <van-goods-action-button type="warning" @click="show = true" text="加入购物车" />
        <van-goods-action-button @click="submitOrder" type="danger" text="立即购买" />
      </van-goods-action>
      <div class="comment">
        <div class="comment-top">
          <span id="comment-num-text">用户评价</span>
          <span id="comment-num">{{ "(" + commentNum + ")" }}</span>
          <span id="select-all" @click="goSelectAllComment">{{ "查看全部 >" }}</span>
        </div>
        <div class="comment-show">
          <CommentUser :user="user" :comment="comment" />
        </div>
      </div>
    </div>
    <van-share-sheet
      v-model="showShare"
      title="立即分享给好友"
      :options="options"
      @select="onSelect"
    />
  </div>
</template>

<script>
import CommentUser from "../../components/CommentUser";
import {
  removeUserCollect,
  addGoodsToShoppingCart,
  addCollect,
  addUserFootprint,
  userCollected,
  getUserShoppingCart,
  getGoodsCommentNumAndFirstComment,
} from "../../api/api.js";
import {
  NavBar,
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Tag,
  ActionSheet,
  Cell,
  Button,
  Notify,
  Skeleton,
  Stepper,
  Icon,
  ShareSheet,
  Dialog,
} from "vant";
export default {
  name: "GoodsDetails",
  components: {
    "van-nav-bar": NavBar,
    "van-goods-action": GoodsAction,
    "van-goods-action-icon": GoodsActionIcon,
    "van-goods-action-button": GoodsActionButton,
    "van-tag": Tag,
    "van-action-sheet": ActionSheet,
    "van-cell": Cell,
    "van-button": Button,
    "van-skeleton": Skeleton,
    "van-stepper": Stepper,
    "van-icon": Icon,
    "van-share-sheet": ShareSheet,
    CommentUser,
  },
  data() {
    return {
      goods: {},
      collected: false,
      collectId: "",
      collectRrompt: false,
      show: false,
      byNumber: 1,
      shoppingCartNum: 0,
      submit: 0,
      isHome: 0,
      commentNum: 0,
      user: {},
      comment: {},
      showShare: false,
      options: [
        [
          { name: "微信", icon: "wechat" },
          { name: "朋友圈", icon: "wechat-moments" },
          { name: "微博", icon: "weibo" },
          { name: "QQ", icon: "qq" },
        ],
        [
          { name: "复制链接", icon: "link" },
          { name: "分享海报", icon: "poster" },
          { name: "二维码", icon: "qrcode" },
          { name: "小程序码", icon: "weapp-qrcode" },
        ],
      ],
    };
  },
  methods: {
    submitOrder() {
      this.show = true;
      this.submit = 1;
    },
    onClickRight() {
      this.showShare = true;
    },
    onClickLeft() {
      if (this.isHome == 0) {
        this.$router.push({
          name: "home",
          params: {
            active: 0,
          },
        });
      } else if (this.isHome == 1) {
        this.$router.push({
          name: "shoppingCart",
          params: {
            active: 1,
          },
        });
      } else if (this.isHome == 2) {
        this.$router.go(-1);
      }
    },
    userRmOrAddCollect() {
      if (this.collected) {
        removeUserCollect(localStorage.getItem("user"), this.collectId).then(() => {
          this.collectRrompt = true;
          setTimeout(() => {
            this.collected = false;
          }, 2000);
          this.collectRrompt = false;
        });
      } else {
        addCollect(localStorage.getItem("user"), this.goods.goodsIdStr).then(() => {
          this.collectRrompt = true;
          setTimeout(() => {
            this.collected = true;
          }, 2000);
          this.collectRrompt = false;
        });
      }
    },
    goShoppingCart() {
      this.$router.push({
        name: "shoppingCart",
        params: {
          active: 1,
        },
      });
    },
    addShoppingCartOrSubmitOrder() {
      if (this.submit == 0) {
        addGoodsToShoppingCart(
          localStorage.getItem("user"),
          this.goods.goodsIdStr,
          this.byNumber
        ).then((val) => {
          if (val.data) {
            Notify({ type: "success", message: "添加成功", duration: 1000 });
          } else {
            Notify("添加失败，请重试");
          }
        });
      } else {
        this.$router.push({
          name: "submitOrder",
          params: {
            goodsNum: this.byNumber,
            shoppingCartList: [
              {
                goods: this.goods,
              },
            ],
            isDetail: true,
            price: this.goods.goodsPrice * this.byNumber * 100,
          },
        });
      }
    },
    onSelect(item) {
      // 默认情况下点击选项时不会自动收起
      // 可以通过 close-on-click-action 属性开启自动收起
      this.show = false;
      Toast(item.name);
    },
    minusByNumber() {
      if (this.byNumber - 1 <= 0) {
        return;
      }
      this.byNumber--;
    },
    addByNumber() {
      if (this.byNumber++ > this.goods.goodsInventory) {
        return;
      }
      this.byNumber + 1;
    },
    goSelectAllComment() {
      this.$router.push({
        name: "selectAllComment",
        params: {
          goods: this.goods,
        },
      });
    },
  },

  mounted() {
    this.goods = { ...this.$route.params.goods };

    if (this.$route.params.isHome != undefined) {
      this.isHome = this.$route.params.isHome;
    }
    getUserShoppingCart(localStorage.getItem("user")).then((val) => {
      // if (val.data == "") {
      //   Dialog.alert({ message: "登录信息失效！请重新登录" }).then(() => {
      //     this.$router.push({
      //       name: "login",
      //     });
      //   });
      // }
      this.shoppingCartNum = val.data.length;
    });
    userCollected(localStorage.getItem("user"), this.goods.goodsIdStr).then((val) => {
      if (val.data != "") {
        this.collected = true;
      }
    });
    addUserFootprint(localStorage.getItem("user"), this.goods.goodsIdStr);

    getGoodsCommentNumAndFirstComment(
      localStorage.getItem("user"),
      this.goods.goodsIdStr
    ).then((value) => {
      this.comment = value.data.comment;
      this.commentNum = value.data.goodsCommentNum;
      this.user = value.data.comment.user;
      this.user.userAvatar = "http://localhost:8080" + this.user.userAvatar;
    });
    this.loading = false;
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
.goodsDetails {
  display: flex;
  position: relative;
}
.goods-img {
  width: 100%;
  top: 30px;
  height: 200px;
  position: absolute;
}
.goods-img > img {
  width: 100%;
  height: 100%;
}
.text {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100px;
  top: 230px;
}
.price {
  display: flex;
  position: absolute;
  left: 40px;
  top: 20px;
}
.price > p {
  position: absolute;
  left: 15px;
  text-align: center;
  font-size: 25px;
  font-weight: 700;
  color: #ff5951;
}
.goods-class {
  display: flex;
  position: absolute;
  top: 60px;
  left: 40px;
}
.goods-name {
  display: flex;
  position: absolute;
  top: 75px;
  left: 40px;
}
.goods-name > p {
  text-align: center;
  font-size: 15px;
  font-weight: 600;
}
.inventory {
  display: flex;
  position: absolute;
  top: 100px;
  left: 25px;
}
.inventory > p {
  text-align: center;
  font-size: 10px;
  color: grey;
}
.selection {
  width: 100%;
  height: 300px;
  position: relative;
}
.selection-img {
  position: absolute;
  width: 70px;
  height: 70px;
  top: 15px;
  left: 10px;
  border: 1px rgba(209, 206, 206, 0.197) solid;
  border-radius: 5px;
}
.selection-img > img {
  width: 100%;
  height: 100%;
}
.selection-price {
  position: absolute;
  top: 30px;
  left: 90px;
  display: flex;
}
#yuan {
  position: absolute;
  color: red;
  top: 5px;
}
#price {
  position: absolute;
  color: red;
  top: 0;
  font-size: 25px;
  left: 15px;
}
.by-number {
  position: absolute;
  top: 100px;
  width: 100%;
}
.selection-btn {
  position: absolute;
  right: 60px;
}
#btn-add {
  position: absolute;
  right: 20px;
}
.number {
  position: absolute;
  bottom: 0;
  width: 15px;
  height: 20px;
  right: 50px;
}
.number > p {
  text-align: center;
  line-height: 20px;
}
#price-yuan {
  position: absolute;
  text-align: center;
  font-size: 15px;
  top: 5px;
  left: 0px;
  font-weight: 700;
  color: #ff5951;
}
.ok {
  position: absolute;
  display: flex;
  width: 100%;
  height: 40px;
  bottom: 0;
}

.comment-show {
  /* 评价 */
  position: absolute;
  left: 10px;
  top: 465px;
  width: 95%;
  height: 100px;
}
.comment-top {
  position: absolute;
  top: 450px;
  width: 100%;
  height: 40px;
  left: 0;
}
#comment-num-text {
  position: absolute;
  left: 20px;
  font-weight: 550;
  font-size: 15px;
}
#select-all {
  position: absolute;
  right: 10px;
  color: grey;
  font-size: 15px;
}
#comment-num {
  position: absolute;
  left: 80px;
  top: 3px;
  font-size: 15px;
  color: grey;
}
</style>
