<template>
  <div>
    <div class="search">
      <p @click="onCancel">&lt;</p>
      <form action="javascript:;">
        <van-search
          v-model="searchValue"
          show-action
          placeholder="请输入搜索关键词"
          @search="onSearch"
          @cancel="onCancel"
          :autofocus="focus"
        />
      </form>
    </div>
    <div class="loading">
      <van-loading size="24px" v-if="loadingFlag">加载中...</van-loading>
    </div>
    <div class="history-search" v-show="!focus"></div>
    <div class="search-hot" v-show="!focus"></div>
    <div class="goods-list" v-if="searchGoodsData.length > 0">
      <van-card
        v-for="(item, index) in searchGoodsData"
        :key="index"
        :price="item.goodsPrice"
        desc="描述信息"
        :title="item.goodsName"
        :thumb="item.goodsImage"
        style="width: 100%"
        @click-thumb="goGoodsDetail(item)"
      >
        <template #tags>
          <van-tag plain size="medium" type="success" color="rgb(7,193,96)">{{
            item.goodsFirstClass
          }}</van-tag>
          <van-tag plain size="medium" type="success" color="rgb(7,193,96)">{{
            item.goodsTwoClass
          }}</van-tag>
        </template>
        <template #footer>
          <van-button
            type="primary"
            color="rgb(64, 174, 54)"
            icon="shopping-cart-o"
            round
            size="mini"
            @click="addToShoppingCart(item)"
          >
          </van-button>
        </template>
      </van-card>
    </div>
    <div v-if="notGoodsFlag" class="not-goods">
      <img :src="notGoods" alt="" />
      <p>抱歉, 没有找到商品哦</p>
    </div>
  </div>
</template>

<script>
import { Search, Card, Tag, Loading, Button, Notify, Empty } from "vant";
import {
  searchGoods,
  goodsClassification,
  addGoodsToShoppingCart,
} from "../../api/api.js";
export default {
  name: "Search",
  components: {
    "van-search": Search,
    "van-card": Card,
    "van-tag": Tag,
    "van-loading": Loading,
    "van-button": Button,
  },
  data() {
    return {
      searchValue: "",
      searchGoodsData: [],
      notGoods: require("../../assets/notGoods.webp"),
      notGoodsFlag: false,
      loadingFlag: false,
      focus: false,
    };
  },
  methods: {
    onSearch() {
      searchGoods(this.searchValue, localStorage.getItem("user")).then((val) => {
        this.loadingFlag = true;
        setTimeout(() => {
          if (val.data.length > 0) {
            this.searchGoodsData = val.data;
            this.notGoodsFlag = false;
            this.loadingFlag = false;
          } else {
            this.loadingFlag = false;
            this.searchGoodsData = [];
            this.notGoodsFlag = true;
          }
        }, 2000);
      });
    },
    onCancel() {
      this.$router.go(-1);
    },
    goGoodsDetail(item) {
      this.$router.push({
        name: "goodsDetails",
        params: {
          goods: {
            ...item,
          },
          isHome: 2,
        },
      });
    },
    addToShoppingCart(goods) {
      addGoodsToShoppingCart(localStorage.getItem("user"), goods.goodsIdStr, 1).then(
        (val) => {
          if (val.data == 1) {
            Notify({ type: "success", message: "添加成功", duration: 1000 });
          } else {
            Notify({ type: "danger", message: "添加失败", duration: 1000 });
          }
        }
      );
    },
  },
  mounted() {
    if (this.$route.params.getClass != undefined) {
      goodsClassification(this.$route.params.getClass, null).then((val) => {
        this.focus = false;
        if (val.data.length > 0) {
          this.searchGoodsData = val.data;
          this.notGoodsFlag = false;
          this.loadingFlag = false;
        } else {
          this.searchGoodsData = [];
          this.notGoodsFlag = true;
        }
      });
    } else {
      this.focus = true;
    }
  },
};
</script>

<style scoped>
.search {
  top: 0;
  left: 30px;
}
.search > p {
  position: absolute;
  top: 17px;
  left: -15px;
  line-height: 20px;
  font-size: 35px;
  color: grey;
}
.not-goods {
  /* 抱歉，没有找到商品哦 */
  position: absolute;
  left: 107.5px;
  top: 400px;
  width: 160px;
  height: 22px;
  font-family: PingFangSC-Regular;
  font-size: 16px;
  font-weight: 0;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0px;
  color: #999999;
}
.not-goods > img {
  /* img */
  position: absolute;
  right: 5px;
  top: -200px;
  width: 125px;
  height: 124.97px;
}
.goods-list {
  display: flex;
  position: absolute;
  left: 0px;
  top: 80px;
  width: 100%;
  justify-content: space-between;
  flex-flow: wrap;
}
.loading {
  display: flex;
  position: absolute;
  top: 50px;
  left: 150px;
}
</style>
